.wp-block-create-block-gus-button {


	.richtext-button {
		color: var(--btn-text-color);
		background-color: var(--btn-background-color);
		border: var(--border-width) solid var(--btn-outline-color);
		border-radius: var(--border-radius);
		padding-left: var(--btn-padding-left);
		padding-right: var(--btn-padding-right);
		padding-top: var(--btn-padding-top);
		padding-bottom: var(--btn-padding-bottom);
		text-decoration: none;
	}

	.richtext-arrow {
		background-color: var(--btn-icon-background-color);
		margin-left: var(--btn-icon-margin-left);
		border: var(--border-icon-width) solid var(--btn-outline-icon-color);
		border-radius: var(--border-icon-radius);
		padding-left: var(--btn-padding-icon-left);
		padding-right: var(--btn-padding-icon-right);
		padding-top: var(--btn-padding-icon-top);
		padding-bottom: var(--btn-padding-icon-bottom);



		&::after {
			display: inline-block;
			height: 1.75rem;
			width: 1.75rem;
			background-size: cover;
			background-color: var(--btn-icon-color);
			-webkit-mask: var(--svg);
			mask: var(--svg);
			content: "";
			position: relative;
			--svg: url("data:image/svg+xml;base64,");
		}
	}

	

		
	.btn-arrow-together.richtext-button {
		align-items: center;
		margin-top: var(--btn-margin-top);

		&:hover {

				background-color: var(--btn-hover-color);
				color: var(--btn-hover-text-color);
			

			i.richtext-arrow {

				background-color: var(--btn-icon-hover-background-color);
				color: var(--btn-icon-hover-text-color);

				&::after {
					background-color: var(--btn-icon-hover-color);

				}

			}

		}
	}

	
}